const tableMixin = {
	props: {   
    // table
    //classes & styles
    tableClass: String,
    tableStyle: String,
    // treeProps
    treeProps:{
      type:Object,
      default: function() {
        return {children: 'children', hasChildren: 'hasChildren'}
      }
    },
    // 
    data: {
      type: Array,
      default: function() {
        return []
      }
    },
    // loading
    vLoading: Boolean,
    // Table 
    size: String,
    // width
    width: [String, Number],
    // height
    height: {
      type:[String, Number],
      default:'100%'
    },
    maxHeight: [String, Number],
    // 
    fit: {
      type: Boolean,
      default: true
    },
    //  table
    stripe: {
      type: Boolean,
      default: true
    },
    // 
    border:{
       type:Boolean,
       default: true
    },
    //  Key
    rowKey: [String, Function],
    context: {},
    // 
    showHeader: {
      type: Boolean,
      default: true
    },
    // 
    showSummary: Boolean,
    // 
    sumText: String,
    // 
    summaryMethod: Function,
    // ClassName & style
    rowClassName: [String, Function],
    rowStyle: [Object, Function],
    cellClassName: [String, Function],
    cellStyle: [Object, Function],
    headerRowClassName: [String, Function],
    headerRowStyle: [Object, Function],
    headerCellClassName: [String, Function],
    headerCellStyle: [Object, Function],
    // 
    highlightCurrentRow: {
      type:Boolean,
      default:true
    },
    //  key
    currentRowKey: [String, Number],
    //  slot="empty" 
    emptyText: String,
    //  Table  row-key  keys 
    expandRowKeys: Array,
    //  Table  type="expand"  Column 
    defaultExpandAll: Boolean,
    //  prop proporder
    defaultSort: Object,
    // tooltip effect 
    tooltipEffect: String,
    // 
    spanMethod: Function,
    //columns
    columns: {
      type: Array,
      default: function() {
        return []
      }
    },
    },
    data: function() {
    return {
      locationData:undefined
    }
  },
   
}
export default tableMixin
