<template>
    <el-container class="container cool-table" style="height:100%">
    <slot name="title"></slot>
    <el-header v-if="tableButtons.length||uploadTool"  style="padding:0px;" height="auto" >
      <el-row>
        <el-col :span="18">
          <!-- button -->
          <cool-button-group ref="tableButtons" v-if="tableButtons.length" :buttons="tableButtons" @button-click="tableButtonsevent"/>
           <el-upload ref="upload" v-if="uploadTool" :action="urlUploadAttachment" :headers="headers" :multiple="multiple" :data="uploadData" :name="name" :with-credentials="withCredentials" :show-file-list="showFileList" :drag="drag" :accept="accept" :on-preview="onPreview" :on-remove="removeChange" :on-success="onSuccess" :on-error="onError" :on-progress ="onProgress " :on-change="uploadListChange" :before-upload="beforeUpload" :before-remove="beforeRemove" :list-type="listType" :auto-upload="autoUpload" :file-list="uploadlist" :http-request="httpRequest" :disabled="uploadChoose" :limit="limit" :on-exceed="onExceed" style="padding:5px 0px 0px 5px">
            <el-button slot="trigger" :size="uploadButtonSize" type="primary" :disabled="uploadChoose">{{uploadChooseText}}</el-button>
            <el-button :disabled="uploadlist.length === 0" :size="uploadButtonSize" type="success" style="margin-left:3px" @click="submitUpload">{{upbtnUploadText}}</el-button>
            <el-button :size="uploadButtonSize" type="danger" :disabled="uploadDelete" @click="delUploaded" style="margin-left:0">{{uploadDeleteText}}</el-button>
            <el-button :size="uploadButtonSize" type="primary" :disabled="uploadDownload" @click="downloadAttachment" style="margin-left:0" v-if="showDownload">{{uploadDownloadText}}</el-button>
            <div slot="tip" class="el-upload__tip">{{uploadTip}}</div>
          </el-upload>
        </el-col>
          <el-col :span="6">
            <slot name="query"></slot>
          </el-col>
      </el-row>
    </el-header>
    <el-main style="padding:0px;border:none;height:100%">
      <el-table ref="table" :class="tableClass" v-loading="vLoading" :style="tableStyle" :data="data" :size="size" :width="width" :height="height" :max-height="maxHeight" :fit="fit" :stripe="stripe" :border="border" :row-key="rowKey" :context="context" :show-header="showHeader" :show-summary="showSummary" :sum-text="sumText" :summary-method="summaryMethod" :row-className="rowClassName" :row-style="rowStyle" :cell-class-name="cellClassName" :cell-style="cellStyle" :header-row-class-name="headerRowClassName" :header-row-style="headerRowStyle" :header-cell-class-name="headerCellClassName" :header-cell-style="headerCellStyle" :highlight-current-row="highlightCurrentRow" :current-row-key="currentRowKey" :empty-text="emptyText" :expand-row-keys="expandRowKeys" :default-expand-all="defaultExpandAll" :default-sort="defaultSort" :tooltip-effect="tooltipEffect" :span-method="spanMethod"> 
        <el-table-column v-for="column in columns" :type="column.type" :label="column.label" :class-name="column.className" element-loading-text=""  element-loading-spinner="el-icon-loading":label-class-name="column.labelClassName" :prop="column.prop" :width="column.width" :min-width="column.minWidth" :sortable="column.sortable" :sort-method="column.sortMethod" :sort-by="column.sortBy" :resizable="column.resizable" :column-key="column.columnKey" :align="column.align" :header-align="column.headerAlign" :show-tooltip-when-overflow="column.showTooltipWhenOverflow" :show-overflow-tooltip="true" :fixed="column.fixed" :formatter="column.formatter" :selectable="column.selectable" :filter-method="column.filterMethod" :filters="column.filters" :filter-placement="column.filterPlacement" :filter-multiple="column.filterMultiple" :index="column.index" :key="column.columnKey">
        <template slot="header" >
          <el-tooltip class="item" effect="dark" :content="column.label" placement="top"><span>{{column.label}}</span></el-tooltip>
        </template>
      </el-table-column>
        <slot name="columns"></slot>
      </el-table>
    </el-main>
    <el-footer height="auto" style="border:1px solid #ebeef5;border-top:0px" v-if="showPage">
      <el-pagination style="float:right" ref="pagination" :class="paginationClass" :style="paginationStyle" :page-size="pageSize"  :total="total" :page-count="pageCount" :current-page="currentPage" :layout="layout" :page-sizes="pageSizes" :popper-class="popperClass" :prev-text="prevText" :next-text="nextText" :background="background"></el-pagination>
    </el-footer>
  </el-container>
</template>

<script>
  // import coolButton from '../cool-button.vue'
const component = {
    name: 'table-view',
    components:{
      // coolButton
    },
    props: {
    // 
    
    //
    uploadTip:String,
    //
    uploadTool:Boolean,
    // 
    urlUploadAttachment:String,
    // 
    uploadChooseText:{
      type:String,
      default:''
    },
    // 
    upbtnUploadText:{
      type:String,
      default:''
    },
    // 
    uploadDeleteText:{
      type:String,
      default:''
    },
    // 
    uploadDownloadText:{
      type:String,
      default:''
    },
    // 
    showDownload:{
      type:Boolean,
      default:true
    },
    // disable
    uploadChoose:Boolean,
    uploadDelete:Boolean,
    uploadDownload:Boolean,
    // 
    multiple:{
      type:Boolean,
      default:true
    },
    // 
    uploadData:Object,
    // 
    name:String,
    //  cookie 
    withCredentials:Boolean,
    // 
    showFileList:{
      type:Boolean,
      default:true,
    },
    // 
    drag:Boolean,
    // 
    accept:String,
    // 
    onPreview:Function,
    // 
    onRemove:Function,
    // 
    onError:Function,
    // 
    onProgress:Function,
    // 
    onChange:Function,
    //  false  Promise  reject
    beforeUpload:Function,
    //  false  Promise  reject
    beforeRemove:Function,
    // 
    listType:{
      type:String,
      default:'text'
    },
    // 
    autoUpload:{
      type:Boolean,
      default:false
    },
    // 
    httpRequest:Function,
    // 
    disabled:{
      type:Boolean,
      default:false,
    },
    // 
    limit:{
      type:Number,
      default:5
    },
    // 
    uploadButtonSize:{
      type:String,
      default:'mini'
    },
    // =================================================================
    
    // button
    tableButtons:{
      type:Array,
      default:function(){
        return []
      }
    },

    // table
    
    // 
    showPage: Boolean,
    //classes & styles
    buttonGroupStyle: String,
    buttonGroupClass: String,
    tableClass: String,
    tableStyle: String,
    paginationClass: String,
    paginationStyle: String,
    // 
    data: {
      type: Array,
      default: function() {
        return []
      }
    },
    // loading
    vLoading: Boolean,
    // Table 
    size: String,
    // width
    width: [String, Number],
    // height
    height: {
      type:[String, Number],
      default:'100%'
    },
    maxHeight: [String, Number],
    // 
    fit: {
      type: Boolean,
      default: true
    },
    //  table
    stripe: Boolean,
    // 
    border:{
       type:Boolean,
       default: true
    },
    //  Key
    rowKey: [String, Function],
    context: {},
    // 
    showHeader: {
      type: Boolean,
      default: true
    },
    // 
    showSummary: Boolean,
    // 
    sumText: String,
    // 
    summaryMethod: Function,
    // ClassName & style
    rowClassName: [String, Function],
    rowStyle: [Object, Function],
    cellClassName: [String, Function],
    cellStyle: [Object, Function],
    headerRowClassName: [String, Function],
    headerRowStyle: [Object, Function],
    headerCellClassName: [String, Function],
    headerCellStyle: [Object, Function],
    // 
    highlightCurrentRow: Boolean,
    //  key
    currentRowKey: [String, Number],
    //  slot="empty" 
    emptyText: String,
    //  Table  row-key  keys 
    expandRowKeys: Array,
    //  Table  type="expand"  Column 
    defaultExpandAll: Boolean,
    //  prop proporder
    defaultSort: Object,
    // tooltip effect 
    tooltipEffect: String,
    // 
    spanMethod: Function,
    //columns
    columns: {
      type: Array,
      default: function() {
        return []
      }
    },
    //pagination
    pageSize: {
      type: Number,
      default: 10
    },
    total: Number,
    pageCount: Number,
    currentPage: {
      type: Number,
      default: 1
    },
    layout: {
      default: 'total,prev, next, jumper'
    },
    pageSizes: {
      type: Array,
      default () {
        return [10, 20, 30, 40, 50, 100]
      }
    },
    popperClass: String,
    prevText: String,
    nextText: String,
    background: Boolean

    },
    data: function() {
    return {
      //upload
      uploadlist: [],
      headers: {
        token: localStorage.token,
        Authorization: localStorage.token,
        AccessControlAllowCredentials: true
      },
    }
  },
  install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
   mounted: function() {
    setTimeout(this.registerEvents, 0)
  },      
  methods: {
    clearSelectionOuter(){
        this.$refs.table.clearSelection();
    },
    clearCurrentRow(){
        this.$refs.table.setCurrentRow();
    },
    redirectEvents: function(source, target, events, prefix) {
      events.forEach(function(event) {
        if (source) {
          source.$on(event, function(args) {
            if (args != null) {
              if (args.data) {
                args.data = source
              }
            }
            target.$emit(prefix + event, args)
          })
        }
      })
    },
    registerEvents: function() {
      var table = this.$refs.table
      if (this.$refs.buttonGroup) {
        var buttons = this.$refs.buttonGroup.$children
        var buttonEvents = ['click']
        buttons.forEach(p => {
          this.redirectEvents(p, this, buttonEvents, 'button-')
        })
      }

      var tableEvents = [
        'select',
        'select-all',
        'selection-change',
        'cell-click',
        'row-click',
        'row-dblclick',
        'header-click',
        'sort-change',
        'current-change'
      ]
      this.redirectEvents(table, this, tableEvents, 'table-')
      var pagination = this.$refs.pagination
      var paginationEvents = ['size-change', 'current-change']
      this.redirectEvents(pagination, this, paginationEvents, 'pagination-')
    },
    tableButtonsevent(arg){
      this.$emit('table-buttons-event',arg)
    },
    onExceed(files, fileList) {
      this.$message.warning(` 5  ${files.length} `);
    },
    onSuccess(response, file, fileList) {
      this.$emit('upload-success',response, file, fileList)
    },
    delUploaded(){
      this.$emit('del-uploaded')
    },
    downloadAttachment(){
      this.$emit('down-load-attachment')
    },
    removeChange(file, fileList) {
      this.uploadlist = fileList
    },
    uploadListChange(file, fileList) {
      this.uploadlist = fileList
    },
    submitUpload() {
      if(this.uploadlist.length!=0){
        console.log(this.uploadlist)
        this.$refs.upload.submit();
      }else{
        this.$message('')
      }
    },
  }
}

export default component
</script>

<style scoped>

</style>